<style lang="less">
@baseColor: #ffc124;
@borderColor: #c8c8c8;
.favorite_box{
	padding: 60px 0;
	.filter_box{
		a{
			display: block;
			float: left;
			color: #333;
			margin-left: 44px;
			line-height: 30px;
			padding: 0 15px;
			cursor: pointer;
			&.active{
				color: @baseColor;
				border-bottom: 2px solid @baseColor;
			}
		}
	}
	.favourite_goods{
		width: 1000px;
		.goods_item{
			margin-top: 40px;
			.cover{
				width: 160px;
				height: 160px;
				padding: 6px;
				border: 1px solid @borderColor;
				border-radius: 3px;
				margin-right: 6px;

				img{
					width: 100%;
				}
			}

			.goods_bd{
				border: 1px solid @borderColor;
				border-radius: 3px;

				&--info{
					h4{
						line-height: 44px;
						font-weight: normal;
						font-size: 20px;
						text-align: center;
						border-bottom: 1px solid @borderColor;
					}
					border-right: 1px solid @borderColor;

					.content{
						color: rgba(0,0,0,0.54);
						padding: 10px;
						line-height: 1.5;
					}
				}

				&--intro{
					width: 100px;
					.icon{
						height: 16px;
						width: 11px;
					}
					div{
						height: 33.3%;
						display: flex;
						justify-content: center;
						align-items: center;
						.exprice{
							background: url('../../../assets/img/userinfosprite.png') -11px -0px no-repeat;
						}
						.address{
							background: url('../../../assets/img/userinfosprite.png') -0px -0px no-repeat;
						}
						.watch{
							height: 12px;
							width: 21px;
							background: url('../../../assets/img/userinfosprite.png') -22px -0px no-repeat;
						}
					}
				}
			}
		}
	}

}
</style>

<template>
	<div class="favorite_box">
		<div class="filter_box flex-wrap">
			<nav v-for="its in nav">
				<a :class="$index === curTab ? 'active' : ''" @click="curTab = $index">{{its.title}}</a>
			</nav>
		</div>
		<div class="favourite_goods" v-for="its in nav[curTab].data">
			<div class="goods_item flex-wrap">
				<div class="cover">
					<img :src="its.informationimage" alt="">
				</div>
				<div class="goods_bd flex-con flex-wrap">
					<div class="goods_bd--info flex-con">
						<h4>{{its.title}}</h4>
						<div class="content">
							<p>{{its.informationdes}}</p>
						</div>
					</div>
					<div class="goods_bd--intro">
						<div>
							<span class="icon exprice"></span>
							<span>{{its.exprice}}</span>
						</div>
						<div>
							<span class="icon address"></span>
							<span>{{its.address}}</span>
						</div>
						<div>
							<span class="icon watch"></span>
							<span>{{its.watch}}</span>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>

export default {
	data() {
		return{
			nav: [{
				title: '全部',
				data: []
			}, {
				title: '出手转让',
				data: []
			}, {
				title: '入手求购',
				data: []
			}, {
				title: '物品租赁',
				data: []
			}, {
				title: '失物招领',
				data: []
			}],
			curTab: 0
		}
	},
	ready() {
		this.getGoods();
	},
	methods: {
		getGoods() {
			const favouriteData = require('../../../../mock/favouritedata').default.data.recordList;
			this.nav[0].data = favouriteData;
			for(let i=0; i<favouriteData.length; i++) {
				if(favouriteData[i].informationtypeid === 1) {
					this.nav[1].data.push(favouriteData[i]);
				} else if(favouriteData[i].informationtypeid === 2) {
					this.nav[2].data.push(favouriteData[i]);
				} else if(favouriteData[i].informationtypeid === 3) {
					this.nav[3].data.push(favouriteData[i]);
				} else if(favouriteData[i].informationtypeid === 4) {
					this.nav[4].data.push(favouriteData[i]);
				}
			}
		}
	}
}
</script>


